<template>
	<div class="transaction-detail">
		<div class="field-part">
			<mt-cell title="卡号" is-link @click.native=" showSelectorPaymentAccount() " 
				:value="  dataForm.account || '请选择卡号' " ></mt-cell>
		</div>

		<div class="field-part">
			<cell-date-zone title="时间跨度" zone="1M, 3M"  ref="dateZone" @date-zone-change=" dateZoneChange " ></cell-date-zone>
			<mt-cell title="起始日期" :value=" dataForm.beginDate ">
			</mt-cell>
			<mt-cell title="结束日期" :value=" dataForm.endDate ">
			</mt-cell>
		</div>

		<div class="field-warn">
			仅支持查询时间跨度但个月内的交易明细记录
		</div>

		<div class="field-action">
			<mt-button @click.native="search()" size="large" type="primary">查询</mt-button>
		</div>

		<selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import { mapGetters, mapActions, mapState } from 'vuex';

import * as servicePayment from '@/services/payment';

export default {
    data() {
        return {
            dataForm: {
                account: '',
		        beginDate: '',
		        endDate: ''
            },
			selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: false,
                accountList: []
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.transactionDetail.search);
        servicePayment.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
        this.$refs.dateZone.setDefault( this.dataForm.beginDate, this.dataForm.endDate );
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            if( this.$validate.isEmpty( _data.account ) ) {
                _error.push( '银行卡号不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }

            this.$router.push( '/transaction-detail/list/' + this.dataForm.account);
        },
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
				this.dataForm.account = data.item.account;
            }, ( data ) => {});
        },
        dateZoneChange ( begin, end ) {
			this.dataForm.beginDate = begin;
			this.dataForm.endDate = end;
		}

    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateTransactionDetailSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearTransactionDetailSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
    .transaction-detail{
    }
</style>


