<template>
	<div class="transaction-detail-list">
		<mt-cell class="card-number" title="卡号">
  			<span>123456789</span>
		</mt-cell>
		<!-- <mt-loadmore class="load-top" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
  			<div class="detail-list">
    			<mt-cell v-for="item in list" :key="item" @click.native="toDetail" :title="item.type" :label="item.time">
		  			<p class="item-p">
		  				<span class="font14">{{item.money1}}</span><br>
		  				<span class="font12 item-p-span">余额：{{item.money2}}</span>
		  			</p>
				</mt-cell>
  			</div>
		</mt-loadmore> -->
        <better-scroll :pullup="true" class="load-top">
            <div class="detail-list">
                <mt-cell v-for="item in list" :key="item" @click.native="toDetail" :title="item.type" :label="item.time">
                    <p class="item-p">
                        <span class="font14">{{item.money1}}</span><br>
                        <span class="font12 item-p-span">余额：{{item.money2}}</span>
                    </p>
                </mt-cell>
            </div>
        </better-scroll>
	</div>
</template>

<script>

import service from '@/services/bank/transaction-detail';

export default {
    data() {
        return {
            list: [],
            allLoaded: false
        };
    },
    methods: {
        toDetail() {
            this.$router.push('/transaction-detail/detail');
        },
        loadTop() {
            this.$refs.loadmore.onTopLoaded();
        },
        loadBottom() {
            this.allLoaded = true;
            this.$refs.loadmore.onBottomLoaded();
        },
        getList(account) {
        	service.getTransactionList({
        		account: account
        	}).then(data => {
        		this.list = data.data.data;
        	});
        }
    },
    created() {
    	this.getList(this.$route.params.account);
    }
};
</script>

<style lang="scss">
.transaction-detail-list{
    height: 100%;
	.card-number{
		width: 100%;
		z-index: 1;
		top:0;
		position: fixed;
	}
	.load-top{
		padding-top: 110px;
        height: 100%;
        overflow: auto;
	}
	.item-p{
		padding-right: 0;
		text-align: right;
	}
	.item-p-span{
		padding-top: 12px;
		display: block;
	}
}
</style>



