<template>
	<div class="transaction-detail-detail">
		<div class="detail">
			<div v-if="detail.account" class="detail-items">
				<mt-cell title="交易账号：" :value="detail.account"></mt-cell>
				<mt-cell title="交易类型：" :value="detail.type"></mt-cell>
				<mt-cell title="交易日期：" :value="detail.date"></mt-cell>
				<mt-cell title="交易时间：" :value="detail.time"></mt-cell>
				<mt-cell title="交易金额：" :value="detail.money1"></mt-cell>
				<mt-cell title="余额：" :value="detail.money2"></mt-cell>
				<mt-cell title="流水号：" :value="detail.number"></mt-cell>
				<mt-cell title="附言：" :value="detail.remark"></mt-cell>
			</div>
			<div v-if="!detail.account" class="detail-items">
				<mt-cell title="暂无交易记录"></mt-cell>
			</div>
		</div>
	</div>
</template>

<script>

import service from '@/services/bank/transaction-detail';

export default {
	data() {
		return {
			detail: {
				account: '',
				type: '',
				date: '',
				time: '',
				money1: '',
				money2: '',
				number: '',
				remark: ''
			}
		};
	},
	methods: {
		getTransactionDetail(id) {
			service.getTransactionDetail({
				id: id
			}).then(data => {
				this.detail = data.data;
			});
		}
	},
	created() {
		this.getTransactionDetail(this.$route.params.id);
	}
};

</script>

<style lang="scss">
	.transaction-detail-detail{
	}
</style>


